// let Login = (resolve) => {
//   return require.ensure([], () => {
//     resolve(require('@/components/login'))
//   })
// }

Vue.use(Router)
Vue.use(zz)

let router = new Router({
  mode: 'history',
  linkActiveClass: 'is-active',
  scrollBehavior (to, from, savedPosition) { // 点击浏览器的前进后退或切换导航时触发
     //console.log(to) // 要进入的路有对象  去向哪里
     //console.log(from) // 要离开的路有对象  从哪里来
    //console.log(savedPosition) // 记录滚动条的坐标 点击前进后退市就会记录
    // if (savedPosition) {
    //    return savedPosition;
    //  } else {
    //   return {x: 0, y: 0}
    // }
    // if (to.hash) { // 利用hash值来定位到某个元素
    //   return {
    //     selector: to.hash
    //   }
    // }
  },
  routes: [
    {
      path: '/',
      component: Homeone,
      children: [
        {
          path: '',
          name: 'Home',
          component: Home,

        },
        {
          path: '/cjly',
          name: 'Cjly',
          component: Cjly
        },
        {
          path: '/hdy',
          name: 'Hdy',
          component: Hdy
        },
        {
          path: '/zby',
          name: 'Zby',
          component: Zby
        },
        {
          path: '/gry',
          name: 'Gry',
          component: Gry
        },
        {
          path: '/qz',
          name: 'Qz',
          component: Qz
        },
        {
          path: '/zyx',
          name: 'Zyx',
          component: Zyx
        },
        {
          path: '/shangpin',
          name: 'Shangpin',
          component: Shangpin,
          meta: {
            // 添加该字段，表示进入这个路由是需要登录的
            requireAuth: true,
          }
        },
        {
          path: '/newslist',
          name: 'Newslist',
          component: Newslist
        },
        {
          path: '/redian',
          component: Redian,
          children: [
            {
              path: '',
              name: 'Redian',
              component: Zby
            }
          ]
        },
        {
          path: '/tuijian',
          component: Tuijian,
          children: [
            {
              path: '',
              name: 'Tuijian',
              component: Hdy
            }
          ]
        },
        {
          path: '/news',
          name: 'News',
          component: News
        }
      ]
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})

router.beforeEach((to, from, next) => {
  let bl = to.matched.some(function(item){
    return item.meta.login;
  })
  if(bl){
    let info = router.app.$local.fetch('miaov')
    if(info.login){
      next();
    }else{
      router.push({
        path: '/login',
        query: {
          redirect: to.path.slice(1)
        }
      })
    }
  }else{
    next();
  }
})

